.modal-box {
  position: absolute;
  z-index: 4;

  &_edit-cell {
		width: 195px;

	}

	&_employees {
    left: 145px;
	}

	&_models {
    left: 690px;
	}

		&__inner {
    box-shadow: 0 5px 10px 0 rgba(0, 0, 0, .1);
    box-sizing: border-box;
    padding: 4px 10px 10px;
    background: #fff;
    border: 1px solid #cdd2d3;
    width: 100%
  }

  &__field {
    border-bottom: 1px solid #cdd2d3;
    margin-bottom: 10px;
    height: 35px;
		margin-right: 10px;

		//modificator
		&_edit-cell {
			border-bottom: 1px solid transparent;
		}
  }
  &__buttons {
    text-align: right;
  }
}


.modal_overlay {
  opacity: 1;
  filter: alpha(opacity=0);
  position: absolute;
  top: 0;
  left: 0;
  z-index: 3;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
}
